/* new-elements */

.new-elements .btn-element {
  padding: 10px 5px;
  background-color: white;
  display: block;
  color: #636a84;
  cursor: move;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-transition: all 0.05s;
  transition: all 0.05s;
  text-align: center;
  border-radius: 0;
  box-shadow: 0 1px 2px 0 #d9d9da;
}

.new-elements .btn-element:hover {
  background-color: #636a84;
  color: white;
}

.new-elements .btn-element:active:not([draggable=false]) {
  box-shadow: inset 0 2px 7px -4px rgba(0, 0, 0, 0.5);
  transform: translateY(1px);
}

.new-elements .btn-element i {
  display: block;
  text-align: center;
  margin: 0 auto;
}

.new-elements .btn-element[draggable=false] {
  opacity: .5;
  cursor: pointer;
}

